.movies-cards {
  padding: 70px 70px 80px;
  margin: 0 auto;
  max-width: 1280px;
  box-sizing: border-box;
}

.movies-cards_type_saved {
  padding-bottom: 187px;
}

.movies-cards__list {
  margin: 0 0 60px;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 37px 24px;
}

.movies-cards__empty {
  margin: 0 auto;
  text-align: center;
}

.movies-cards__button {
  padding: 10px 0;
  border: none;
  width: 100%;
  background-color: #2f2f2f;
  font-weight: 500;
  font-size: 12px;
  line-height: 16px;
  text-align: center;
  color: #FFFFFF;
  border-radius: 6px;
}

@media screen and (max-width: 1279px) {
  .movies-cards {
    padding-left: 30px;
    padding-right: 30px;
  }

  .movies-cards_type_saved {
    padding-bottom: 90px;
  }
  
  .movies-cards__list {
    margin-bottom: 80px;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}

@media screen and (max-width: 767px) {
  .movies-cards {
    padding-top: 40px;
    padding-left: 10px;
    padding-right: 10px;
  }

  .movies-cards__list {
    margin-bottom: 75px;
    grid-template-columns: 1fr;
    gap: 14px;
  }
}